<template>
  <div id="app">
    <div class="nav">
      <ul class="container">
        <li :class="stat==0?'active':''" @click="stat=0"><router-link to="/">首页</router-link></li>
        <li :class="stat==1?'active':''" @click="stat=1"><router-link to="/playList">歌单</router-link></li>
        <li :class="stat==2?'active':''" @click="stat=2"><router-link to="/bangdan">榜单</router-link></li>
        <li><select name="" id="">
          <option value="">酷狗</option>
          <option value="">网易</option>
        </select>
          <input v-model="keywords" type="text">
          <button><router-link :to="{name:'search',params:{keywords:keywords}}">搜索</router-link></button>
        </li>
      </ul>
    </div>
    <span class="old">
      <a href="/old">返回旧版首页>></a><br>
      <a href="/video">进入视频解析>></a>
    </span>
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      stat: 0,
      keywords: ''
    }
  }
}
</script>

<style>
  .old{
    position: fixed;
    right: 3%;
    top: 40%;
    z-index: 999;
  }
  .old a{
    color: #009af3;
    font-weight: bold;
  }
  .nav{
    width: 100%;
    background: #000000;
    height: 50px;
    display: inline-block;
  }
  .nav li{
    text-align: center;
    line-height: 50px;
    width: 10%;
    display: inline-block;
  }
  .nav li:last-child{
    width: 50%;
  }
  .nav li:hover,.nav li.active{
    background: #158fe1;
  }
  .nav li:last-child:hover{background:#000}
  .nav li:last-child *{color:#333}
  .nav li select{
    width: 60px;
    font-size: 14px;
    border: none;
    border-radius: 3px;
    height: 25px;
  }
  .nav li input{
    width: 150px;
    border: 0;
    height: 28px;
    border-radius: 14px;
    padding-left: 10px;
  }
  .nav li button{
    border: 0;
    border-radius: 3px;
    padding:5px 10px;
  }
  .nav li *{
    color: #fff;
  }
  html,body,#app{
    width: 100%;
    height: 100%;
  }
.container {
  width: 1200px;
  margin: 0 auto;
}
  *{
    font-family: 微软雅黑;
    color: #333333;
    margin: 0;
    padding: 0;
    list-style: none;
    text-decoration: none;
  }
</style>



